import store from '../../store/index.js'
import SET from '../../SET.js'
export default {
	config: {
		// baseUrl: "https://hp.fjhjc.net/Data",
		baseUrl: SET.baseUrl,
		
		// baseUrl: "https://testapi.kuxiong999.com",   //
		header: {
			'Content-Type': 'application/json;charset=UTF-8',
			// 'Content-Type':'application/x-www-form-urlencoded'
		},
		data: {},
		method: "GET",
		dataType: "json",
		/* json JSON.parse */
		responseType: "text",
		success() {},
		fail() {},
		complete() {
			
		}
	},
	interceptor: {
		request: function(config, ifLoad) {
			const value = uni.getStorageSync('access_token');
			// console.log(value)
			if (value) {
				config.header.Authorization = value
			}
			return config;
		}, 
		response: function(response, ifLoad) {
			let statusCode = response.statusCode
			let that = this
			// console.log(response)
			// 
			if (statusCode === 200) { //
				return response.data
			} else if (statusCode === 401) {
			 // if (true) {	
				   if(!store.state.hasLogin)return
					uni.showModal({
						title: ' ',
						content: ',',
						showCancel:false,
						success: function (res) {
							if (res.confirm) {
								uni.removeStorageSync('hepai_token');
								uni.removeStorageSync('user');
								uni.navigateTo({
									url: '/pages/role/login/login'
								});
							}
						}
					});	
					return ''
			} else if(response.errMsg=="request:fail abort"){
				




				
				let ifLock = uni.getStorageSync('errlock')
				console.log(ifLock)
				// uni.setStorageSync('errlock',false)
				if(!ifLock){
					uni.setStorageSync('errlock',true)
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/errors/errors',
						})
						
					},1000)
					
				}
				
				
				return ''
			}else{
				
				return ''
			}

		}
	},
	request(options, ifLoad) {
		let that = this
		if (!options) {
			options = {}
		}
		options.baseUrl = options.baseUrl || this.config.baseUrl
		options.dataType = options.dataType || this.config.dataType
		options.url = options.baseUrl + options.url
		options.data = options.data || {}
		options.method = options.method || this.config.method
		options.timeout = 10000
		// console.log('')
	
		return new Promise((resolve, reject) => {
			let _config = null
			
			options.complete = (response) => {
				let statusCode = response.statusCode
				// console.log(response)
				// response.config = _config
				if (this.interceptor.response) {
					// reject('')
					let re = this.interceptor.response(response, ifLoad)
					// console.log(re)
					if (re === '') {
						reject('');
						return;
					}else if (re === '') {
						reject('');
						return;
					}else if (re === '') {
						reject('');
						return;
					}else{
						resolve(re);
					}
				}

			}

			_config = Object.assign({}, this.config, options)
			_config.requestId = new Date().getTime()
			// console.log(_config)
			if (this.interceptor.request) {
				let re = this.interceptor.request(_config, ifLoad)
				_config = re
			}
			

			uni.request(_config);
		});
	}
}


